.container{
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    position: relative;
    height: 32px;
    width: 100%;
    justify-content: space-between;
}
.container:hover{
    background: #666666;
    color: #f5f5f5;
}
.imageContainer{
    height: 100%;
    min-width: 128px;
}
.image{
    height: 100%;
    width: 32px;
    object-fit: cover;
}
.title{
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}